@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Estonia&family=Shalimar&family=Tangerine:wght@400;700&family=ZCOOL+XiaoWei&display=swap);
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.App {
	text-align: center;
}

.nav {
	position: sticky;
	top: 0;
	z-index: 99;
	background-color: #ffffff;
	display: flex;
	justify-content: space-between;
	padding: 1rem 4rem;
	align-items: center;
	height: 100%;
}
#logo {
	width: 15rem;
	
}

.link-con {
	list-style: none;
	display: flex;
}

.link-con * {
	margin-left: 1.3rem;
}

.link-con *:hover {
	color: salmon;
	cursor: pointer;
}
/* Main */

.main-con h1 {
font-family: 'Lobster', cursive;
font-size: 4rem;
padding: 1rem;
color: rgb(90, 13, 13);
}

.menu {
	padding: 5rem 0rem ;
	height: 40rem;
}

.card-con {
	display: flex;
	justify-content: space-around;	
	margin-top: 3rem;
}

.menu-card {
	width: 10rem;
	height: 15rem;
	border: 2px solid green;

}

#menu-btn {
	margin: 2rem;
	padding: 0.6rem 4rem;
	font-size: 1.4rem;
}



.footer {
	height: 10rem;
	background-color: rgb(0, 49, 0);
	color: #ffffff;
	display: flex;
	justify-content: center;
	align-items: center;
}

.here {
	width: 4rem;
	height: 4rem;
	border-radius: 50%;
	position: sticky;
	bottom: 1rem;
	float: right;
	background-color: salmon;
	display: flex;
	justify-content: center;
	align-items: center;
	cursor: pointer;
	font-size: 0.7rem;
}

.jumbotron {
	max-height:50rem;
	object-position: center;
	object-fit: contain;
}

.jumbotron-img {
	max-height: 50rem;
	object-fit: contain;
}

.jumbotron-img img {
	object-fit: contain;
}
.location-title {
	display: flex;
	align-items: center;
	justify-content: center;
	border-top: 5px dotted rgb(250, 166, 177);
		border-bottom: 5px dotted rgb(250, 166, 177);
}
.location-title div {
	margin-right: 2rem;
}

.days-locations {
margin-top: 1rem;
}

iframe {
	width: 80%;
	height: 600px;
}

.days-locations button {
	padding: 0.7rem 2rem;
	margin:  0.5rem 0.2rem;
	background-color: green;
	color: #ffffff;
	border-radius: 10px;
	border: none;
	cursor: pointer;
	font-size: 1.3rem;
}
.days-locations button:hover {
		background-color: rgb(240, 154, 154);
}

#map {
	margin-bottom: 5rem;
}

a {
	text-decoration: none;
	color: #000000;
	font-size: 1.1rem;

}

/* Service */
.services {
	background-image: url(/static/media/bg-service.3e94fca0.png);
}

#trans {
	background-color: #4b4b4b85;
	height: 100%;
	padding: 5rem 0rem;
}
.services {
	height: 42rem;
	margin-bottom: 2rem;
}

.services h1 {
	color: #ffffff;
	font-size: 3rem;
} 

#service-btn {
	margin-top: 1rem;
	padding: 0.7rem 2rem ;
	font-size: 1.4rem;
}


.ser-card {
	width: 20rem;
	height: 20rem;
	background-color: #ffffffb2;
	padding-top: 1.3rem;

}
.ser-card ul {
	margin-top: 2rem;
}

.ser-card li {
text-align: left;
padding: 0 2rem;
}

#moving-truck {
	width: 10rem;
}

.page-container{
    /* background-image: url(https://cdn1.site-media.eu/images/512/1568226/ptn_02.jpg); */
height: 60rem;
}
.about-us{

width:100%;
 margin: 0 auto;
}
.container{
    display: flex;
    justify-content: center;
    grid-gap: 50px;
    gap: 50px;
    width: 80%;
   margin: 4rem auto;
} 
.about-us-content{
	font-size: 2rem;
	font-family: 'Tangerine', cursive;
}
.container img{
	margin-top:5%;
	width: 30rem;
	height: 20rem;
}
.header{
    margin: 3rem auto 1rem auto;
	text-align: center;
	font-size: 2.5rem;
	font-family: 'Trebuchet W01 Regular';
	width:70%
}
.header span{
	color: rgb(207, 68, 53);
}
.icons{
    position: absolute;
    top: 50%;
    display: flex;
    flex-direction: column;
    grid-gap: 20px;
    gap: 20px;
}

.icons{
	font-size: 50px;
	font-weight: 800;
	
}

.icons i:hover{
	color: salmon;
}

.page-container{
	position: relative;
}
